import React, {useEffect, useState} from "react";
import apiRequest from "../addition-components/apiRequest";
import SortContainer from "../addition-components/SortContainer";
import Answer from "./Answer";

function Answers({ questionId }) {
  const [states, setStates] = useState({
    result: [],
    sortType: 'votes',
    orderType: 'desc',
    page: 1
  });

  useEffect(() => {
    apiRequest(`questions/${questionId}/answers`, states, setStates,
      {
        'filter': '!t)IWEyFiVBr1g2gi-RKES99AD2RF(wH',
      }
    );
  }, [states.orderType, states.sortType, states.page]);

  return (
    <div className='answers'>
      <h2>Answers</h2>
      <SortContainer
        setState={setStates}
        sortBy={["votes", "activity", "creation"]}
        orderBy={["desc", "asc"]}
      />
      {
        states.result && states.result?.length > 0 ?
          states.result.map((answer, index) =>
            answer ? <Answer key={index} answer={answer} /> : null)
          : null
      }
    </div>
  );
}

export default Answers;