import React, {useState, useEffect} from 'react';
import {Link} from "react-router-dom";
import apiRequest from "../addition-components/apiRequest";
import '../css/question-page.css'
import dateGoodView from "../addition-components/dateGoodView";
import Answers from "./Answers";
import apiActionRequest from "./apiActionRequest";

function apiActionPrepare(url, task, setStates) {
  let token = localStorage.getItem('userToken');

  if (token) {
    apiActionRequest(url, task, setStates,
      {
        'site': 'stackoverflow',
        'key': 'xzf5GeIyy1QHmRTuxM3ZjA((',
        'access_token': token,
      }
    );
  } else
    alert('You must to be authorized for this action');
}

function QuestionRate({ question }) {
  const [states, setStates] = useState({
    action: {},
    upButton: false,
    downButton: false,
  });

  useEffect(() => {
    console.log(states.action);
  }, [states.action]);

  function upVote(e) {
    let url = `questions/${question?.question_id}/upvote${states.upButton ? '/undo' : ''}`;
    let action = states.upButton ? 'upvote-undo' : 'upvote';

    apiActionPrepare(url, action, setStates);
  }

  function downVote(e) {
    let url = `questions/${question?.question_id}/downvote${states.upButton ? '/undo' : ''}`;
    let action = states.upButton ? 'downvote-undo' : 'downvote';

    apiActionPrepare(url, action, setStates);
  }

  return (
    <div className='question-rate'>
      <div>
        <button onClick={upVote} className='action-button'>
          <img className='top-img' src="/images/down-arrow.png" alt="like"/>
        </button>
        <p>{question?.score}</p>
        <button onClick={downVote} className='action-button'>
          <img src="/images/down-arrow.png" alt="dislike"/>
        </button>
      </div>
    </div>
  )
}

function QuestionContainer({ question }) {
  return (
    <div className='question-container'>
      <QuestionRate question={question} />

      <div className='question-summary'>
        <div className='question-text'>
          <p className='question-title'>{question?.title}</p>
          <p className='question-numbers'>
            <span>Asked: {dateGoodView(question?.creation_date)}</span>
            <span>Answers: {question?.answer_count ?? 0}</span>
          </p>
          <p className='question-body' dangerouslySetInnerHTML={{__html: question?.body}} />
        </div>
        <div className='tags-and-user-info'>
          <div className="tags">
            {question.tags.map((tag, index) => (
              <span key={index} className="tag">{tag}</span>
            ))}
          </div>
          <div className='user-info'>
            <div className="user-details">
              <Link to={`/users/${question?.owner?.user_id}`}>
                {question?.owner?.display_name}
              </Link>
              <div>
                <span className="reputation-score">{question?.owner?.reputation}</span>
              </div>
            </div>
            <div className="user-icon-block">
              <div className="avatar-wrapper-32">
                <Link to={`/users/${question?.owner?.user_id}`}>
                  <img
                    src={question?.owner?.profile_image}
                    style={{width: "32px", height: "32px"}}
                    className="user-icon"
                  />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

function Question({ match }) {
  let questionId = match.params.id;

  const [states, setStates] = useState({
    result: []
  });

  useEffect(() => {
    if (questionId)
      apiRequest(`questions/${questionId}`, states, setStates,
        {'filter': '!2.jDQvoPjNGlykDlONa4S'}
      );
  }, []);

  return (
    <div className="questions-container">
      {
        states?.result && states?.result?.length === 1 ?
          (
            <>
              <QuestionContainer question={states.result[0]} />
              <Answers questionId={states.result[0]?.question_id}/>
            </>
          )
          : null
      }
    </div>
  )
}

export default Question;