import React, {useState} from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import './css/style.css';
import Header from "./addition-components/Header";
import Footer from "./addition-components/Footer";
import Main from "./questions-page/Main";
import Users from "./users-page/Users";
import Tags from "./tags-page/Tags";
import Profile from "./profile/Profile";
import Question from "./question-page/Question";
import Search from "./questions-page/Search";

function App() {
  return (
    <>
      <Router>
        <Header />
          <div className="App">
            <Switch>
              <Route path="/" exact component={Main} />
              <Route path="/questions/" exact component={Main} />
              <Route path="/questions/tag/:id" component={Main} />
              <Route path="/questions/:id" component={Question} />
              <Route path="/search/:id" component={Search} />
              <Route path="/users" exact component={Users} />
              <Route path="/users/:id" component={Profile} />
              <Route path="/profile" component={Profile} />
              <Route path="/tags" component={Tags} />
            </Switch>
          </div>
        <Footer />
      </Router>
    </>
  );
}

export default App;