async function apiActionRequest(whatAsk, action, setStates, parameters) {
  let requestString = `https://api.stackexchange.com/2.2/${whatAsk}`;

  const data = await fetch(requestString, {
    method: "POST",
    headers: {
      'Content-Type': 'application/json',

    },
    body: JSON.stringify(parameters)
  });

  const apiResult = (await data.json());

  if (!apiResult || apiResult.error_id) {
    alert(apiResult.error_message);
  } else {
    setStates(states => ({
      ...states,
      'action': {
        'action': action,
        'data': apiResult,
      }
    }));
  }

}

export default apiActionRequest;