import React, {useEffect, useState} from "react";
import LoadMore from "../addition-components/LoadMore";
import apiRequest from "../addition-components/apiRequest";
import Comment from "./Comment";

function ToggleButton({ propName, setStates }) {
  function toggle(e) {
    console.log('OK')
    setStates(prev => ({
      ...prev,
      [propName]: !prev[propName]
    }))
  }

  return (
    <div className='toggle-container'>
      <span onClick={toggle} className='comments-toggler'>Show comments</span>
    </div>
  )
}

function Comments({ answerId }) {
  const [states, setStates] = useState({
    result: [],
    showComments: false,
    page: 1
  });

  useEffect(() => {
    if (states.showComments) {
      apiRequest(`answers/${answerId}/comments`, states, setStates,
        {
          'filter': '!t)IWEyFiV9alxJ46W-60LO8KvaTQ*Po',
        }
      );
    }
  }, [states.page, states.showComments]);

  return (
    <div className='comments'>
      <ToggleButton propName='showComments' states={states} setStates={setStates} />
      {
        states.showComments ?
          <>
            {
              states?.result && states.result.length ?
                states.result.map((comment, index) =>
                  comment ? (<Comment key={index} comment={comment} />) : null
                )
                : null
            }
            <LoadMore value={states} setValue={setStates} />
          </>
          : null
      }
    </div>
  )
}

export default Comments;