import dateGoodView from "../addition-components/dateGoodView";
import {Link} from "react-router-dom";
import React from "react";

function CommentRate({ comment }) {
  return (
    <div className='comment-rate'>
      <div>
        <button className='action-button'>
          <img className='top-img' src="/images/down-arrow.png" alt="like"/>
        </button>
        <p>{comment.score}</p>
        <button className='action-button'>
          <img src="/images/down-arrow.png" alt="dislike"/>
        </button>
      </div>
    </div>
  )
}

function Comment({ comment }) {
  return (
    <div className='comment-container'>
      <CommentRate comment={comment} />
      <div className='comment-summary'>
        <div className='comment-text'>
          <p className='comment-body' dangerouslySetInnerHTML={{__html: comment?.body}} />
        </div>
        <div className='addition-comment-info'>
          <div>
            <p>commented: {dateGoodView(comment?.creation_date)}</p>
          </div>
          <div className='user-info'>
            <div className="user-details">
              <Link to={`/users/${comment?.owner?.user_id}`}>
                {comment?.owner?.display_name}
              </Link>
              <div>
                <span className="reputation-score">{comment?.owner?.reputation}</span>
              </div>
            </div>
            <div className="user-icon-block">
              <div className="avatar-wrapper-32">
                <Link to={`/users/${comment?.owner?.user_id}`}>
                  <img
                    src={comment?.owner?.profile_image}
                    style={{width: "32px", height: "32px"}}
                    className="user-icon"
                    alt='profile'
                  />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Comment;